<template>
    <div class="toollist">
        <div class="list">
            <van-col span="24" style="margin-bottom:0.2rem">
                <div class="topbar" @click="change">
                    <div class="bar">
                        <van-col style="text-align:left" span="12">
                            <span>筛选</span>
                        </van-col>
                        <van-col span="12" style="font-size:0.28rem;text-align:right">
                            <span class="class">{{category}}</span>
                            <van-icon name="arrow-down"></van-icon>
                        </van-col>
                    </div>
                    <ul>
                        <li :class="index == active ? 'active' : ''" class="classList" v-for="(list,index) in classList" :key="index">{{list}}</li>
                    </ul>
                </div>
            </van-col>
        </div>
    </div>
</template>

<script>
var uid = Options.GetUserInfo().openid
export default {
    name:'toollist',
    data () {
        return {
            toollist:'',//工具列表
            category:'全部',//筛选内容
            classList:['全部','运动健身','英语学习','艺术','创业/职场','生活情感','养生'],
            active:-1,
        }
    },
    created(){
        this.tool(this)
    },
    methods:{
        tool(app){
            TD_Request('op','opt',{uid:uid,catalog:false},function(code,data){
                console.log(data)
                app.toollist = data.accounts.data;
            },function(code,data){
                console.log(data)
            })
        },
        change(){
            $('ul').slideToggle();
        },
        select(index,item){
            this.active = index;
            this.category = item;
            $('ul').slideUp();
        }
    }
}
</script>

<style lang="less" scoped>
    .toollist{
        width: 100%;
        height:100%;
        .list{
            width: 6.9rem;
            margin: 0.2rem auto 0;
            .topbar{
                background: #fff;
                height: auto;
                border-radius: 0.1rem;
                line-height: 0.86rem;
                overflow: hidden;
                .bar{
                    width: 6.3rem;
                    margin: 0 auto;
                    span{
                        font-size: 0.28rem;
                        color: #999;
                    }
                    .class{
                        color: #00d094;
                    }
                }
                ul{
                    display: none;
                }
                .classList{
                    width: 100%;
                    border-top:1px solid #eee;
                    height: 0.85rem;
                    line-height: 0.85rem;
                    text-align: left;
                    width: 6.3rem;
                    margin: 0 auto;
                    font-size: 0.28rem;
                }
                .classList.active{
                    color: #00d094;
                }
            }
        }
    }
</style>



